<template>
    <div :class="{[name]:true}" :style="{height,overflow:'hidden',position: 'relative'}" >
        <!-- {{name}} -->
      <ul>
        <!-- 这里去遍历store里的cinemasData数据 -->
        <slot></slot>
        <div v-for="item in $store.state.cinemasData" :key="item.cinemaId" >
            <li v-if="!isInclude||isInclude.cinemaList.includes(item.cinemaId)" @click="choiceCinema(item.cinemaId)">
              <div class="cinema-info-contianer">
                <span class="cinema-info">{{ item.name }}</span>
                <span class="address cinema-info">{{ item.address }}</span>
              </div>
              <div>
                <span style="color: green">￥{{ item.lowPrice / 100 }}起</span>
                <span style="text-align: right" class="address">距离未知</span>
              </div>
            </li>
        </div>
      </ul>
    </div>
</template>
<script>


import BetterScroll from "better-scroll";


export default {
    props:{
      height:{
        type:String,
        default:'0px'
      },
        name:{
            type:String,
            default:'box'
        },
        isInclude:{
            type:Object,
            default:null
        }
    },
  data() {
    return {
      filmName:''
    };
  },

  mounted() {
    

      // 调用actions的getCinemasData方法  所有组件共享
      if(this.$store.state.cinemasData.length===0){
        // console.log(this.$store.dispatch('getCinemasData',this.$store.state.cityId));
        this.$store.dispatch('getCinemasData',this.$store.state.cityId).then(()=>{
          this.$nextTick(()=>{
            new BetterScroll("."+this.name, {
          scrollbar: {
            fade: true,
          },
          mouseWheel: true,
          click: true
        });
          })
      })
      }else{
        this.$nextTick(()=>{
            new BetterScroll("."+this.name, {
          scrollbar: {
            fade: true,
          },
          mouseWheel: true,
          click: true
        });
          })
      }
      
      


    // http({
    //   url: `/gateway?cityId=${this.$store.state.cityId}&ticketFlag=1&k=4705870`,
    //   headers: {
    //     "X-Host": "mall.film-ticket.cinema.list",
    //   },
    // }).then((res) => {
    //   this.cinemas = res.data.data.cinemas;
    //   // console.log(res.data.data.cinames);
    //   this.$nextTick(() => {
    //     new BetterScroll(".box", {
    //       scrollbar: {
    //         fade: true,
    //       },
    //       mouseWheel: true,
    //     });
    //   });
    // })
  },
  methods:{
    choiceCinema(cinemaId){
      
          this.$router.push({
            name:'影院',
            params:{
              cinemaId
            }
          })
        },
    clickCity(){
      this.$router.push({
        path:'/city'
      });
    },
    searchCinema(){
      this.$router.push({
          path:'/cinemas/search'
        })
    }
  }
};
</script>
<style lang="scss" scoped>
ul{
  background-color: #fff;
  
}
ul>div:last-child {
    margin-bottom: 3.0625rem;
  }
li {
    font-size: .9375rem;
color: #191a1b;
  height: 4.7188rem;
  width: 24.375rem;
  box-sizing: border-box;
  padding: 0.9375rem;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #ededed;
  span {
    display: block;
  }
  .cinema-info-contianer {
    width: 80%;
    padding-right: 15px;
    box-sizing: border-box;
  }
  .cinema-info {
    // max-width: 80%;
    overflow: hidden;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
.address {
  color: #797d82;
  font-size: 0.75rem;
  margin-top: 0.3125rem;
}

.box {
  position: relative;
  overflow: hidden;
}

</style>
