<template>
  <div>
    <ul class="filmsDetail">
      <li
        v-for="item in dataList"
        :key="item.filmId"
        @click="handleChangePage(item.filmId)"
      >
        <img :src="item.poster" alt="" />
        <div class="FilmInformation">
          <div>
            <span class="name">{{ item.name }} </span
            ><span class="item">{{ item.filmType.name }}</span>
          </div>
          <div>
            <span class="label"
              >观众评分
              <span style="color: #ffb232">{{ item.grade }}</span></span
            >
          </div>
          <div>
            <span class="label"
              >主演：<span v-if="item.actors">{{ item.actors | actors }}</span
              ><span v-else>暂无人员</span></span
            >
          </div>
          <div>
            <span class="label"
              >{{ item.nation }} | {{ item.runtime }}分钟</span
            >
          </div>
        </div>
        <div class="buy" @click="buyTicket(item.filmId,item.name)">购票</div>
      </li>
    </ul>
    <div v-if="show" class="noMore">暂无更多</div>
  </div>
</template>
<script>
import http from "@/util/http.js";
import Vue from "vue";
Vue.filter("actors", (v) => {
  return v.map((item) => item.name).join(" ");
});
export default {
  data() {
    return {
      dataList: [],
      page: 1,
      tag: true,
      show:true
    };
  },
  methods: {
    buyTicket(id,name){
      this.$store.commit('setFilmId',id)
      this.$router.push({
        // name: "详情", //跳往组件的名字
        name:'电影视图',   //用params时不能用path
        params: {
          // path: `/detail/${id}/cinemas`,
          filmName:name,
          myID: id,
        },
      });
    },
    handleChangePage(id) {
      // 编程式导航
      // location.href = '#/detail'     可能会出问题

      // 这里的this.$router是main中的router模块
      // 1.通过路由跳转
      // this.$router.push(`/detail/${id}`)

      // 2.通过路由命名跳转
      this.$router.push({
        name: "详情", //跳往组件的名字
        params: {
          //跳往的path
          //es6 成对象属性    这里的id必须和动态路由那里一样
          
          myID: id,
        },
      });
    },
    sendRequest() {
      http({
        // 这里不需要再写请求地址了
        url: `/gateway?cityId=${this.$store.state.cityId}&pageNum=${this.page++}&pageSize=10&type=1&k=70052`,
        headers: {
          "X-Host": "mall.film-ticket.film.list",

        },
      }).then((res) => {
        this.dataList.push(...res.data.data.films)
        if (this.dataList.length===res.data.data.total) {
          this.tag = false;
        }
      });
    },
  },
  mounted() {
    const noMore = document.querySelector('.noMore')
    const ob = new IntersectionObserver(entries=>{
      //isIntersecting true为进入 false离开 进入离开都会触发
      // console.log(entries[0].isIntersecting);     
      if(entries[0].isIntersecting && this.tag){
        this.sendRequest()
      }
    },{ 
      root:null,     //null为与视口重叠
      thresholds:1
    })
    ob.observe(noMore)
  },
};
</script>

<style lang="scss" scoped>
.filmsDetail li {
  background-color: #fff;
  height: 7.75rem;
  padding: .9375rem;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  img {
    // height: ;
    width: 4.125rem;
  }
  .FilmInformation {
    padding: 0 .625rem;
    box-sizing: border-box;
    width: 15.25rem;
    .name {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      display: inline-block;
      margin-right: 5px;
      color: #191a1b;
      max-width: 12.4375rem;
      vertical-align: middle;
    }
    .item {
      display: inline-block;
      font-size: 12px;
      color: #fff;
      background-color: #d2d6dc;
      height: 14px;
      line-height: 14px;
      padding: 0 2px;
      border-radius: 2px;
      vertical-align: middle;
    }
    div {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      width: 14rem;
    }
  }
  .label {
    font-size: .8125rem;
    margin-top: .25rem;
    color: #797d82;
  }
}
.buy {
  flex: 1;
  height: 1.5625rem;
  border: 1px solid green;
  line-height: 1.5625rem;
  color: green;
  font-size: 13px;
  text-align: center;
  border-radius: 2px;
}
.noMore{
  height:1.5rem;width:24.375rem;
  color: #d2d6dc;
  text-align: center;
  line-height: 1.5rem;
}
</style>
