<template>
  <div class="main">
    <van-nav-bar
      :title="'影院'"
      id="navBar"
      @click-left="clickCity"
      @click-right="searchCinema"
    >
      <template #left>
        {{ $store.state.cityName }}
        <van-icon name="arrow-down" color="black" />
      </template>
      <template #right>
        <van-icon name="search" size="22" color="black" />
      </template>
    </van-nav-bar>

    <CinemaList :height = height></CinemaList>
  </div>
</template>
<script>
import CinemaList from "@/components/CinemaList";

export default {
  components: {
    CinemaList,
  },
  data(){
    return {
      height:'0px'
    }
  },
  mounted() {
    const html = document.documentElement
    const htmlSize = parseInt(getComputedStyle(html).fontSize)
    this.height = html.clientHeight - 3.0625*htmlSize - 2.875*htmlSize +'px'
  },
  methods: {
    clickCity() {
      this.$router.push({
        path: "/city",
      });
    },
    searchCinema() {
      this.$router.push({
        path: "/cinemas/search",
      });
    },
  },
  
};
</script>
<style lang="scss" scoped>
.main{
  background: #fff;
}
</style>
