<template>
  <div  v-if="cityList.length">
    <div class="city-list">
    <div class="header"><span class="i" @click="$router.back()"><van-icon name="cross" /></span><span>当前城市 - {{this.$store.state.cityName}}</span>
    <span></span></div>
    <van-search v-model="value" placeholder="请输入搜索关键词" :show-action="show" @focus="show = true"/>


<!-- {{filterCity()}} -->
    </div>

      <van-index-bar v-if="!value" class="cityBar" :index-list="computedList" @select="currrenIndex" highlight-color='green'>
        <div v-for="(item,index) in cityList" :key="index">
        <van-index-anchor :index="item.type" />
        <van-cell  v-for="v in item.newList" :title="v.name" :key="v.cityId" @click="selectCity(v)"/>
        </div>
      </van-index-bar>
      <van-cell v-else v-for="v in filterCity()" :title="v.name" :key="v.cityId" @click="selectCity(v)"/>
  </div>
</template>
<script>
// import { IndexBar, IndexAnchor } from "vant";
import http from '@/util/http.js'
import {Toast} from 'vant'
export default {
    data(){
        return {
            value:"",
            cityList:[],
            searchList:[],
            show:false
        }
    },

    
    mounted(){
        http({
            //cityId
            url:`/gateway?cityId=${130600}&ticketFlag=1&k=1173969`,
            headers:{'X-Host': 'mall.film-ticket.city.list'}
        }).then(res=>{
            this.searchList = res.data.data.cities
            this.cityList = this.cityFilter(res.data.data.cities)
            this.$nextTick(() => {

        })
        })
    },
    computed:{
        computedList(){
            return this.cityList.map(v=>v.type)
        }
    },
    methods:{
        filterCity(){
            return this.searchList.filter(v=>{
                return v.name.toUpperCase().includes(this.value.toUpperCase()) || v.pinyin.toUpperCase().includes(this.value.toUpperCase())
            })
        },
        selectCity(city){
            this.$store.commit('selectCity')
            // this.$store.state.cityId = city.cityId;
            this.$store.commit('changeCityId',city.cityId)
            // this.$store.state.cityName = city.name;
            this.$store.commit('changeCityName',city.name)
            this.$router.back()
        },
        currrenIndex(index){
            Toast({
                message: index,
                duration:1000

            })
        },
        cityFilter(cities){
            
            let dataList = [];
            let letter =[];
                for(let i = 65;i<91;i++){
                   letter.push(String.fromCharCode(i))
                }
            letter.forEach(v=>{
                let list = cities.filter(item=>item.pinyin[0].toUpperCase() ===v)                
                list.length > 0 && dataList.push({type:v,newList:[...list]})
            })
            return [...dataList]
        }
    },
};
</script>
<style lang="scss" scoped>
</style>
<style>
.van-toast--html, .van-toast--text{
    min-width: 2rem;
}
.van-index-anchor--sticky{
    background: #f4f4f4;
    top: 6.125rem;
}
</style>
<style scoped lang='scss'>
.city-list{
    z-index: 10;
    position: sticky;
    top: 0px;
}
.city-list .header {
    .i{
        font-size: 1.625rem;
        position: absolute;
        left: .3125rem; 
        color: gray;
    }
    // position: relative;
    width: 100%;
    height: 2.75rem;
    overflow: hidden;
    background-color: #fff;
    line-height: 2.75rem;
    text-align: center;
    font-size: 1.0625rem;
}
</style>