import Vue from 'vue'
import VueRouter from 'vue-router'
import Center from '@/views/Center'
import Info from '@/views/info'
import SignOut from '@/views/Center/SignOut'
import Cinemas from '@/views/Cinemas'
import Search from '@/views/Cinemas/search'
import ChoiceCinema from '@/views/Cinemas/choiceCinema'
import detail from "@/views/detail"
import Login from "@/views/Login"
import order from "@/views/order"
import Films from '@/views/Films'
import City from '@/views/City'
import Schedule from '@/views/schedule'
import comingsoon from "@/views/Films/comingsoon"
import nowPlaying from "@/views/Films/nowPlaying"
import FilmCinemas from '@/views/Films/filmView/filmCinemas'

// import { component } from 'vue/types/umd'
// import { sign } from 'core-js/core/number'

Vue.use(VueRouter)  //注册路由组件，两个全局 router-view  router-link


//配置表
const routes = [
  {
    path:'/schedule/:scheduleId',
    component:Schedule,
    name:'schedule'
  },
  {
    path:'/info',
    component:Info,
    meta:{
      isMain:true,
    }
  },
  {
    path:'/city',
    component:City
    
  },
  {
    //当路径为/films加载Films组件
    path: '/films',
    component: Films,
    
    children: [
      {
        path: '/films/comingsoon',
        component: comingsoon,
        meta:{
          isMain:true,
        },
      },
      {
        path: '/films/nowPlaying',
        component: nowPlaying,
        meta:{
          isMain:true,
        },
      },
      {
        path: '/films',
        redirect: '/films/nowPlaying'
      }
    ]
  },
  {
    path: '/center',
    component: Center,
    meta:{
      isMain:true,
    },
    children:[
      {
        path:'/center/signOut',
        component:SignOut,
      },
      // {
      //   path:'/center',
      //   component:SignOut
      // }
    ]
  //   beforeEnter:(to,from,next)=>{             //局部路由守卫
  //     if(localStorage.getItem('token')){        //有该属性的为需要检测是否授权放行
  //       next()
  //     }else{
  //     next({
  //       path:'/login',
  //       query:{redirect:to.fullPath}
  //     })
  //   }
  // }
  },
  {
    path: '/cinemas',
    meta:{
      isMain:true,
    },
    // 路由懒加载 箭头函数返回值


                                      // component: ()=>
                                      //   import('@/views/Cinemas'),
    component:Cinemas,
  },
  {
    name:'影院',
    path:'/cinemas/:cinemaId/film',
    component:ChoiceCinema
  },
  {
    path:'/cinemas/search',
    component:Search
  },
  {
    path:'/Login',
    component:Login
  },
  {
    path:'/order',
    component:order,
    meta:{
      isNext:true     //路由独享拦截
    }
  },
  {
    name:'详情',
    path: '/detail/:myID',  //设置动态路由  detail必须要有二级路由才跳转组件  接着他被route记录了
    component: detail,
  },
  {
    name:'电影视图',
    path: '/detail/:myID/cinemas',
    component: FilmCinemas
  },
  {
    path: '*',// 没有匹配路径时匹配该组件
    redirect: "/center"
  }
]

const router = new VueRouter({
  mode:'hash',//hash   history需要后台支持 因为我们的应用是单页客户端 当搜索不到静态资源时我们应该返回同一个index页面     history没有#号更美观 带#的一定时前端路由  #做一些网页分享时可能会带来意想不到的后果比如拼接字符串
  routes
})

// router.beforeEach((to,from,next)=>{   //全局前置守卫
//   if(to.meta.isNext){   //有该属性的为需要检测是否授权放行
//     if(localStorage.getItem('token')){
//       next()
//     }else{
//       // next('/login')
//       next({
//         path:'/login',
//         query:{redirect:to.fullPath}  //记录着原本next直接去的路径 这里是被path带过来的
//       })
//     }
//   }else{
//     next()
//   }


router.beforeEach((to,from,next)=>{   //全局前置守卫
  
  if(to.meta.isMain){   //有该属性的为需要检测是否授权放行
    router.app.$options.store.state.isFooterShow ||
    router.app.$options.store.commit('footerShow')
    next()
  }else{
    router.app.$options.store.state.isFooterShow &&
    router.app.$options.store.commit('footerHide') 
    next()
  }
})

  
// })
//导出路由
export default router
