<template>
    <div class="main" ref="main">
        <div class="header">
            <span class="i" @click="$router.back()">
                <van-icon name="arrow-left" />
            </span>
            <span>{{$route.params.filmName}}</span>
            <span><van-icon name="search" size="22" color="black"/></span>
        </div>
         
    <van-tabs swipeable v-if="showCinemas.length" title-active-color="green">
        
            <van-tab v-for="(cinemas,index) in data" :title="cinemas" :key="index">
                <CinemaList :name="cinemas" :isInclude="showCinemas[index]" :height = height>
                    <div>
                        <div class="tab">全城<van-icon name="arrow-down" size='10px'/></div>
                        <div class="tab">最近去过<van-icon name="arrow-down" size='10px'/></div>
                    </div>
                </CinemaList>
            </van-tab>
    </van-tabs>
</div>
</template>
<script>
import http from '@/util/http.js'
import CinemaList from "@/components/CinemaList"
import moment from 'moment'

export default {
    components:{
        CinemaList
    },
    computed:{
        
    },
    data(){
        return {
            showCinemas:[],
            data:[],
            height:'0px'
        }
    },
    mounted(){
        const html = document.documentElement
    const htmlSize = parseInt(getComputedStyle(html).fontSize)
    this.height = html.clientHeight- 3.0625*htmlSize - 2.875*htmlSize +'px'
    console.log(this.height);

        http({
        url: `/gateway/?filmId=${this.$route.params.myID}&cityId=${this.$store.state.cityId}&k=1035572`,
        headers: { "X-Host": "mall.film-ticket.cinema.film-show-cinema" },
      }).then(res=>{
        console.log(res.data.data.showCinemas) 
        this.showCinemas = res.data.data.showCinemas.sort((a,b)=>{
            return a.showDate -b.showDate
        })

        this.data = this.Calculation()
        // console.log(this.Calculation())
      })
      
    },
    methods:{
        Calculation(){
         return this.showCinemas.map(v=>{
                return v.showDate*1000
            }).sort((a,b)=>a - b).map(data=> {
            let time = moment(data).format();
            let today = new Date().getDate()
            let d = new Date(time);
            
            
            let week = moment(data).day();
            let getWeet =(function(){
                switch (week) {
                        case 1:
                        return '周一'
                        case 2:
                        return '周二'
                        case 3:
                        return '周三'
                        case 4:
                        return '周四'
                        case 5:
                        return '周五'
                        case 6:
                        return '周六'
                        case 0:
                        return '周日'
                    }
                })();
                if(d.getDate()-today===0)
                    return '今天'+ (d.getMonth() + 1) + "月" + d.getDate() + "日"
                if(d.getDate()-today===1)
                    return '明天'+ (d.getMonth() + 1) + "月" + d.getDate() + "日"
                if(d.getDate()-today===2)
                    return '后天'+ (d.getMonth() + 1) + "月" + d.getDate() + "日"
                return getWeet+ (d.getMonth() + 1) + "月" + d.getDate() + "日";
            })
        }
    },
}
</script>
<style lang="scss" scoped>
.header {
    display: flex;
    padding: 0 .625rem;
    box-sizing: border-box;
    justify-content: space-between;
    .i{
        font-size: 1.625rem;
        color: gray;
    }
    // position: relative;
    width: 100%;
    height: 2.75rem;
    overflow: hidden;
    background-color: #fff;
    line-height: 2.75rem;
    text-align: center;
    font-size: 1.0625rem;
}.date{
    height: 3.0625rem;
    width: 24.375rem;
    // background: 
}
.main{
    background: #fff;
}
</style>
<style>
.van-tabs__line{
    background-color: green;
    width: 5rem;
    height: .0938rem;
}
.tab{
    display: inline-block;
    /* float: left; */
    font-size: .875rem;
    color: #191a1b;
    height: 3.0625rem;
    width: 50%;
    line-height: 3.0625rem;
    text-align: center;
    border-top: 1px solid #ededed;
    border-bottom: 1px solid #ededed;
}
</style>