<template>
        <van-tabs swipeable animated @change='choose' swipe-threshold=3 title-active-color='green'>
        <van-tab  v-for="(item,index) in filmsInfo[currentIndex].showDate" :title="currenDate(item,true)"   :key="index">
            <div class="schedule-item" v-for="(item) in schedules[index]" :key="item.scheduleId">
                    <div class="left"><span class="top">{{currenDate(item.showAt,false)}}</span><span class="bottom">{{currenDate(item.endAt,false)}}散场</span>
                    </div>
                    <div class="middle"><span class="top">{{item.filmLanguage}}{{item.imagery}}</span>
                    <span class="bottom">{{item.hallName}}</span>
                    </div>
                    <div class="right"><div class="lowest-price"><span class="price-icon">￥</span>{{item.salePrice/100}}</div><div  class="buy-ticket" @click="buyTicket(item.scheduleId)">购票</div>
                    </div>
            </div>
            <div v-if="!schedules[index]" class="null">
                <img src="https://assets.maizuo.com/h5/v5/public/app/img/emptyFilmList.4e4721ad.png" alt="">
                <div class="text">暂无场次</div></div>
        </van-tab>
        </van-tabs> 
</template>
<script>
import moment from 'moment'
import http from '@/util/http.js'
export default {
    props:['filmsInfo','currentIndex','cinemaInfo'],
    // props:['data'],
    data(){
        return {
            schedules:new Array(this.filmsInfo[this.currentIndex].showDate.length),
            tag:true,
        }
    },
    methods:{
        buyTicket(scheduleId){
            // this.$store.commit('setCinemaId',this.cinemaInfo.cinemaId)
            this.$router.push({
                name:'schedule',
                params:{
                    scheduleId
                }
            }).catch(() => {})
        },
        choose(index){
            if(!this.schedules[index])
            this.schedulesList(index)
        },
        schedulesList(index){
            
            http({
            url:`/gateway/?filmId=${this.filmsInfo[this.currentIndex].filmId}&cinemaId=${this.cinemaInfo.cinemaId}&date=${this.filmsInfo[this.currentIndex].showDate[index]}&k=1024902`,
            headers:{
                'X-Host': 'mall.film-ticket.schedule.list',
                'X-Token': 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJhZ2VudElkIjoiMzAwMCIsInRpbWVzdGFtcCI6MTY2NDM0NzU3NCwidXNlcklkIjozMTkyMDU5N30.25T6y93Pt5RFo5U-Z5jJBtkKIo4RATWrY-vcHHmXSD4'
            }
            }).then(res=>{
                if(res.data.data)
                this.$set(this.schedules, index , res.data.data.schedules)
                // this.schedules[index] = res.data.data.schedules
            })
        },
        currenDate(time,tag){
      var date = new Date(time*1000);
      
    //   var Y = date.getFullYear() + '-';
      var M = (date.getMonth() + 1 < 10 ? '' + (date.getMonth() + 1) : date.getMonth() + 1) + '月';
      var D = (date.getDate() < 10 ? '' + (date.getDate()) : date.getDate()) + '日';

      var h = (date.getHours() < 10 ? '0' + (date.getHours()) : date.getHours()) + ':';
      var m = (date.getMinutes() < 10 ? '0' + (date.getMinutes()) : date.getMinutes());
    //   var s = (date.getSeconds() < 10 ? '0' + (date.getSeconds()) : date.getSeconds());

      function getWeek(date) {
        let week = moment(date).day();
        // let today = new Date().getDate()
        // console.log(date - Date.now());
        if(date - Date.now()>-86400000){
        if(date - Date.now()<0)return '今天'
        if(date - Date.now()<86400000)return '明天'
        if(date - Date.now()<86400000*2)return '后天'
        }
        if(week == 0) return "星期日"
        if(week == 1) return "星期一"
        if(week == 2) return "星期二"
        if(week == 3) return "星期三"
        if(week == 4) return "星期四"
        if(week == 5) return "星期五"
        if(week == 6) return "星期六"
    }
    if(tag)
      return getWeek(time*1000)+M+D;
      return h+m
        },
    },
    mounted(){

        this.schedulesList(0)
        
    }
}
</script>

<style scoped lang='scss'>
.null{
    height: 11.25rem;
    padding: 2.75rem 0;
    text-align: center;
    img{
        height: 4.375rem;
            margin: 0 auto 10px;
    }
    .text{
    text-align: center;
    color: #bdc0c5;
    font-size: 14px;
    }
}
div{
    box-sizing: border-box;
}
.schedule-item{
    background: #fff;
    padding: 15px;
    width: 100%;
    height: 74px;
    div{
        float: left;
    }
    .top{
        font-size: 15px;
    color: #191a1b;
    overflow: hidden;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
    }
    .bottom{
            font-size: 13px;
            color: #797d82;
            margin-top: 2px;
        }
    span{
        display: block;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    .left{
        width: 84px;
    }
    .middle{
        width: calc(100% - 210px);
    }
    .right{
        float: right;
        width: 112px;
        padding: 10px 0;
        .lowest-price{
            font-size: 15px;
            line-height: 25px;
            color: green;
            float: left;
        }
        .price-icon{
            font-size: 12px;
            display:inline;
            vertical-align: top;
        }
        .buy-ticket{
            float: right;
            line-height: 25px;
            text-align: center;
            font-size: 12px;
            color: green;
            height: 25px;
            width: 50px;
            border-radius: 2px;
            position: relative;
            text-align: center;
            border: 1px solid green;
        }
    }
}
 @keyframes run{
            0%{
                /* opacity: 0; */

                transform: translateX(-6.25rem);
                
            }
            100%
            {

                transform: translateX(0rem);
                /* transform: translateX(0%); */
            }
        }
.h-enter-active{
            animation: .3s run ease;
}
/* .h-leave-active{
            animation: .3s run ease;
} */
</style>