<template>
    <div v-if="schedule" class="main">
        <div class="header">
            <span><van-icon name="arrow-left" size=24 @click="$router.back()"/></span>
            {{schedule.cinema.name}}
        </div>
        
        <van-notice-bar left-icon="volume-o" :scrollable="false" background='#fbf4d8;' color='#e68e1a' >
            <van-swipe
                vertical
                class="notice-swipe"
                :autoplay="3000"
                :show-indicators="false"
            >
                <van-swipe-item v-for="(item,index) in schedule.noticeMsg.split('，')" :key="index">
                    {{item}}
                </van-swipe-item>
            </van-swipe>
        </van-notice-bar>

        <div class="seating-chart-wrap" v-if="seatingChart.seats" @touchmove='end' @touchend='tranY=0'>
            <div class="screen">{{schedule.hall.name}}</div>
            <div class="rowNav" :style="{height:`${seatingChart.height*24}px`,transform:`translateY(${tranY}px) scale(${currentScale})`}">
                <div v-for="item in seatingChart.height" :key="item">{{item}}</div>
            </div>
            <div  class="seating-chart" :style="{width:`${(seatingChart.width+2)*24}px`,height:`${seatingChart.height*24+24}px`,padding:`0 ${24*mapScale}px`}" ref="seatMap">
                <div v-for="(item,index) in seatingChart.seats" :key="index" :style="{left:24*(item.columnNum)+'px',top:24*(item.rowNum-1)+'px',}">
                        <svg class="icon" aria-hidden="true" width=100% height=100%  @click='helo(index,item.columnNum,item.rowNum)'>
                            <use v-if="item.isBroken" xlink:href="#icon-chupiaosuoding"></use>
                            <use v-else-if="item.isOccupied" xlink:href="#icon-yishou"></use>
                            <use v-else-if="item.isChoose" xlink:href="#icon-xuanzhong"></use>
                            <use v-else xlink:href="#icon-kexuanzuowei"></use>
                        </svg>
                </div>
            </div>
        </div>
        <div class="seat-status-tips">
            <ul>
                <li>
                    <svg class="icon" aria-hidden="true" width=100% height=100%>
                            <use xlink:href="#icon-chupiaosuoding"></use>
                        </svg>
                        不可选
                </li>
                <li>
                    <svg class="icon" aria-hidden="true" width=100% height=100%>
                            <use xlink:href="#icon-yishou"></use>
                        </svg>
                        已售
                </li>
                <li>
                    <svg class="icon" aria-hidden="true" width=100% height=100%>
                            <use xlink:href="#icon-kexuanzuowei"></use>
                        </svg>
                        可选
                </li>
                <li>
                    
                        <svg class="icon" aria-hidden="true" width=100% height=100%>
                            <use xlink:href="#icon-xuanzhong"></use>
                        </svg>
                        选中
                </li>
            </ul>
            
            <div class="input-wrap">
                <div class="container">
                    <div class="cinema-info"><div class="film-name">{{schedule.film.name}}<span  class="switch"> 切换场次 <van-icon name="arrow-down" /></span></div>
                    <div class="film-des">{{currenDate(schedule.showAt)}} {{schedule.film.language}}{{schedule.imagery}}</div>
                    </div>
                    <div class="choose" v-if="choose.length">
                        <div v-for="(item,index) in choose" :key="index" class="choose-item">
                            <div class="seat-name">{{item.row}}排{{item.column}}座</div>
                            <div class="price-fmt">￥{{schedule.price.sale/100}}</div>
                            <van-icon name="cross" class="delete" @click="deleteSeat(index)"/>
                        </div>
                    </div>
                </div>
            </div>

            <div class="confirm-btn" :class="{canSubmit:choose.length}" @click="submit">
                <span v-if="!choose.length">请先选座</span>
                <span v-else>{{schedule.price.sale/100*choose.length}}元 确认收货</span>
            </div>
        </div>
        
    </div>
</template>
<script>
import http from "@/util/http.js"
import '@/assets/font_ia27i42ocvd/iconfont.js'
import { EasyScroller } from 'easyscroller'
import moment from 'moment'
import {Toast} from 'vant'
export default {
    beforeRouteEnter(to,from,next){
    if(localStorage.getItem('userPhone')){
        next()
      }else{
      next({
        path:'/Login',
        query:{redirect:to.fullPath}
        })
        }
    },

    data(){
        return {
            schedule:null,
            seatingChart:[],
            mapScale:null,
            currentScale:null,
            scroller:null,
            count:0,
            tranY:0,
            choose:[]
        }
    },
    mounted(){
        http({
            url:`/gateway/?scheduleId=${this.$route.params.scheduleId}&k=4878872`,
            headers:{
                "X-Host": "mall.film-ticket.schedule.info"
            }
        }).then(res=>{
            this.schedule = res.data.data.schedule
            console.log(this.schedule);
        })
        http({
            url:`/gateway/?scheduleId=${this.$route.params.scheduleId}&k=4878872`,
            headers:{
                "X-Host": "mall.film-ticket.seat.list"
            }
        }).then(res=>{
            this.seatingChart = res.data.data.seatingChart
            // console.log(this.seatingChart.length);
            this.mapScale = this.currentScale= document.documentElement.clientWidth / (this.seatingChart.width+2) /24
            let that =this

            this.$nextTick(()=>{
                this.refu()
            })
            
        })
    },
    methods:{
        submit(){
            if(this.choose.length)
            Toast('暂未接入支付');
        },
        deleteSeat(index){
            this.seatingChart.seats[this.choose[index].seatsIndex].isChoose =false
            this.choose.splice(index,1)
        },
        currenDate(time){
      var date = new Date(time*1000);
      var M = (date.getMonth() + 1 < 10 ? '' + (date.getMonth() + 1) : date.getMonth() + 1) + '月';
      var D = (date.getDate() < 10 ? '' + (date.getDate()) : date.getDate()) + '日';

      var h = (date.getHours() < 10 ? '0' + (date.getHours()) : date.getHours()) + ':';
      var m = (date.getMinutes() < 10 ? '0' + (date.getMinutes()) : date.getMinutes());

      function getWeek(date) {
        let week = moment(date).day();
        if(week == 0) return "星期日"
        if(week == 1) return "星期一"
        if(week == 2) return "星期二"
        if(week == 3) return "星期三"
        if(week == 4) return "星期四"
        if(week == 5) return "星期五"
        if(week == 6) return "星期六"
    }
      return getWeek(date)+M+D+h+m
    },
        end(e){
            // console.log(e);
            this.count++
            if(e.touches.length>=2){
                this.currentScale = getComputedStyle(this.$refs.seatMap).transform.slice(7,-2).split(',')[0]
                this.tranY = getComputedStyle(this.$refs.seatMap).transform.slice(7,-2).split(',')[5]
            }
        },
        refu(){
            let that =this
                    let element = document.querySelector('.seating-chart')
                        if (element) {
                            // ionContent.value.$el.scrollToBottom()
                            that.scroller = new EasyScroller(element, {
                            scrollingX: true,
                            // scrollingY: true,
                            zooming: true,
                            minZoom: that.mapScale,
                            maxZoom: 2.0,
                            zoomLevel: that.mapScale,
                            animating:true
                            });
                            return 0
                        }
                       requestAnimationFrame(this.refu) 
        },
        helo(index,c,r){
            if(!this.seatingChart.seats[index].isBroken&&!this.seatingChart.seats[index].isOccupied){
                if(this.choose.length<5){
                    this.$set(this.seatingChart.seats[index],'isChoose',!this.seatingChart.seats[index].isChoose);
                }else{
                    Toast('最多只能选择5个座位');
                }
            }
            if(this.seatingChart.seats[index].isChoose){
                this.choose.push({column:c,row:r,seatsIndex:index})
            }else if(this.seatingChart.seats[index].isChoose===false){
                this.deleteSeat(this.choose.findIndex(v=>v.seatsIndex===index))
            }
        },
    },
    beforeDestroy(){
        
    }

}
</script>
<style lang="scss" scoped>

.main{
    min-height: 100vh;
}
.header{
    background: #fff;
    height:2.75rem;
    position: relative;
    text-align: center;
    line-height: 2.75rem;
        color: #191a1b;
        font-size: 1.0625rem;
    span{
        line-height: 1.5rem;
        position: absolute;
        left: .625rem;
        top: 50%;
        transform: translateY(-50%);
    }
}

 .notice-swipe {
    height: 2.5rem;
    line-height: 2.5rem;
  }
  .seating-chart-wrap{
    padding-top: 10%;
    background-color: #f6f6f6;
    position: relative;
    overflow:  hidden; 
    height: calc(100vh - 249px);
  }
  .seating-chart{
    position: relative;
    div{
        position: absolute;
        height: 1.5rem;
        width: 1.5rem;
        display: inline-block;
        padding: .0938rem;
    }
  }
  .map{
    position: absolute;
    height: 100%;
    width: 100%;
    transform-origin: 0 0;
  }
  .rowNav{
    transform-origin: 0 0;
    width: 1rem;
    position: absolute;
    background: #000;
    opacity: .2;
    display: flex;
    flex-direction: column;
    color: #fff;
    border-radius: .4688rem;
    div{
        text-align: center;
        font-size: .75rem;
        height: 2.25rem;
        line-height: 24px;
    }
  }
  .screen{
    height: 1.125rem;
    width: 9.375rem;
    position: absolute;
    top: 0;
    text-align: center;
    background: #000;
    opacity: .2;
    color: #fff;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 0 0 .3125rem .3125rem;
    font-size: .75rem;
  }
  .seat-status-tips{
    background: #f6f6f6;
    width: 100%;
    position: absolute;
    bottom: 0px;
    // height: 165px;
    ul{
        display: flex;
        justify-content: center;
        background: #f6f6f6;
    }
    li{
        display: flex;
        height: 1.5rem;
        width: 3.75rem;
        font-size: .75rem;
        line-height: 1.5rem;
        margin-right: 1rem;
        justify-content: center;
        svg{
            width: 1.25rem;
            height: 1.25rem;
        }
    }
  }
  .input-wrap{
    // height: 69px;
    padding: .625rem 0 ;
    border-radius: .3125rem;
    margin: .625rem .625rem 0 .625rem;
  }
  .cinema-info{
    padding: .9375rem 0;

  }
  .switch{
    float: right;
    font-size: .8125rem;
    color: #ff5f16;
  }
  .film-des{
        font-size: .8125rem;
    line-height: 1.1875rem;
    margin-top: .25rem;
    color: #797d82;
  }
  ::-webkit-scrollbar{
        display: none;
        }
  .choose{
    padding: .625rem 0;
    display: flex;
    overflow: auto;
    
    .choose-item{
        position: relative;
        background-color: hsla(0,0%,95.7%,.6);
        height: 3.2188rem;
        min-width: 4.9644rem;
        padding: .25rem 1.5625rem .25rem .625rem;
        margin-right: .9375rem;
        flex-shrink: 0;
    .seat-name{
        font-size: .875rem;
        color: #191a1b;
    }
    .price-fmt{
        color:  rgb(250, 182, 70);
        font-size: .9375rem;
    }
    .delete{
        position: absolute;
        right: 6px;
        bottom: 6px;
    }
    }
  }
  .container{
        background: #fff;
        padding: 0 15px;
  }
  .confirm-btn{
    height: 2.8125rem;
    width: 100%;
    font-size: .9375rem;
    line-height: 1.3125rem;
    padding: .875rem 0;
    text-align: center;
    background-color: #ff5f16;
    color: hsla(0,0%,100%,.3);
    z-index: 2;
  }
  .canSubmit{
    color:#fff
}
</style>