import Vue from 'vue'
import Vuex from 'vuex'

import http from "@/util/http.js";

Vue.use(Vuex)

export default new Vuex.Store({
  // 所有组件都可以访问的公共数据
  state: {
    cityId:110100,
    cityName:'北京',
    cinemaId:null,
    cinemasData:[],
    filmId:undefined,
    isFooterShow:true
  },
  getters: {
  },
  // 只支持同步任务
  mutations: {
    // 直接修改公共状态会导致不可溯源 无法得知被何时修改 这里的修改会被devtools记录
    changeCityId(state,cityId){
      state.cityId = cityId
    },
    changeCityName(state,cityName){
      state.cityName = cityName
    },
    setCinemasData(state,res){
      state.cinemasData = res.data.data.cinemas
    },
    selectCity(store){
      store.cinemasData = [];
    },
    footerHide(store){
      store.isFooterShow = false
    },
    footerShow(store){
      store.isFooterShow = true
    },
    setFilmId(store,id){
      store.filmId = id
    },
    setCinemaId(store,id){
      store.cinemaId = id
    }
  },
  //支持异步和同步
  actions: {
    getCinemasData(store){
      return http({
        url: `/gateway?cityId=${store.state.cityId}&ticketFlag=1&k=4705870`,
        headers: {
          "X-Host": "mall.film-ticket.cinema.list",
        },
      }).then(res=>{
        // 通过commit去挂载cinemasData数据
        store.commit("setCinemasData",res);
      })
    },

  },
  modules: {
  }
})
